import React, { Component } from 'react'
import axios from 'axios';
import {server} from '../../config/keys';
import jwt_decode from 'jwt-decode';
import iziToast from 'izitoast';

import {getValue} from '../../App';

class AddEvent extends Component {
  constructor() {
    super();
    this.state = {
      title: '',
      desc: '',
      people: [],
      value: ''
    }
    this.onChange = this.onChange.bind(this);
    this.onSubmit = this.onSubmit.bind(this);
    this.onChangeArray = this.onChangeArray.bind(this);
    this.removeChild = this.removeChild.bind(this);
    this._goBack = this._goBack.bind(this);
  }
  componentWillMount() {
    if(localStorage.admin && localStorage.auth) {
        const decoded = jwt_decode(localStorage.auth);
        const currentTime = Date.now() /1000;
        if(decoded.exp <  currentTime) {
            this.props.history.push('/admin/login');    
        } 
    } else {
        this.props.history.push('/admin/login');
    } 
  }
  onChange(e) {
    this.setState({[e.target.name]: e.target.value})
  }
  onSubmit(e) {
    e.preventDefault();
    axios.post(`${server}/admin/create`,{title: this.state.title, desc: this.state.desc, people: this.state.people} , {
      headers: {
        'Authorization': localStorage.auth
      }
    })
    .then(res => {
        iziToast.success({
            title: 'Saved',
            message: res.data.success
        })
        this.setState({title: '',desc: '',people: [],value: ''})
        getValue();
    })
    .catch(err => iziToast.error({
        title: 'Error',
        message: err
    }));
  }
  onChangeArray(e) {
    e.preventDefault();
    let value = this.state.value;
    let people =  this.state.people;
    people.push(value);
    this.setState({people,value: ''})
  }
  removeChild(e) {
    let array = this.state.people;
    array = array.filter(val => {
        return val !== e.target.id
    });
    this.setState({people: array});
  }
    _goBack(e) {
        this.props.history.push('/admin/dashboard')
    }
  render() {
    const jsx_array = this.state.people.map(val => {
        return (
            <div className="list_group" key={Math.random()}>
                <li className="list" key={Math.random()}>{val}</li>
                <span id={val} className="list_delete" key={Math.random()} onClick={this.removeChild}>*</span>
            </div>
        )
    })
    let title = `Title: ${this.state.title}` ;
    let desc = `Description: ${this.state.desc}`;
    return (
      <div className="flex_group">
        <div className="flex_one">
            <form onSubmit={this.onSubmit}>
                <input 
                    type="text" 
                    name="title" 
                    value={this.state.title} 
                    className="input" 
                    onChange={this.onChange}
                    placeholder="Title"
                />
                <input 
                    type="text" 
                    name="desc" 
                    value={this.state.desc} 
                    className="input" 
                    onChange={this.onChange} 
                    placeholder="Description"
                />
                <div className="input-grp">
                    <input 
                        type="text" 
                        name="value"
                        value={this.state.value}
                        className="input input_control"
                        onChange={this.onChange}
                        placeholder="Add value to array"
                    />
                    <button className="input-grp-btn" onClick={this.onChangeArray}>Add</button>
                </div>
                <input type="submit" vlaue="submit" className="btn btn_flex" />
            </form>
        </div>
        <button className="btn-back btn" onClick={this._goBack}>Back</button>
        <div className="flex_two">
            <h1>{title}</h1>
            <h2>{desc}</h2>
            <ul>
                {jsx_array}
            </ul>
        </div>
      </div>
    )
  }
}

export default AddEvent;