import React from 'react';
import PropTypes from 'prop-types';


const Nav = ({types, changeCurrent}) => {
    return (
        types.map(val => {
            return (
                <button 
                    className="nav_btn_list" onClick={changeCurrent} name={val.title} key={Math.random()}>{val.title}
                </button>
            )
        })
    )
}
Nav.porpTypes = {
    types: PropTypes.array.ieRequired,
    click: PropTypes.func.isRequired
}

export default Nav;