import {SET_EVENT_DETAIL} from '../actions/types';
const initailState = {
    data: [],
    types: [
        'IT_MANAGER',
        'CODING',
        'GAMING',
        'ICE_BREAKER',
        'WEB_DESIGN',
        'TEMPLE_RUN',
        'VLOG',
        'PAPER_PRESENTATION'
    ]
};

export default function(state = initailState, action) {
    switch(action.type) {
        case SET_EVENT_DETAIL: {
            return {
                ...state,
                data: action.payload
            }
        }
        default :
            return state
    }
}